
"use client";

import { useEffect, useState } from "react";

interface Tour {
  id: string;
  title: string;
  steps: number;
  visibility: "public" | "private";
  createdAt: string;
  views: number;
}

const mockTours: Tour[] = [
  {
    id: "1",
    title: "Demo: Onboarding Flow",
    steps: 5,
    visibility: "public",
    createdAt: "2025-08-20",
    views: 123,
  },
  {
    id: "2",
    title: "Feature Walkthrough",
    steps: 3,
    visibility: "private",
    createdAt: "2025-08-22",
    views: 45,
  },
];

export default function DashboardPage() {
  const [tours, setTours] = useState<Tour[]>([]);

  useEffect(() => {
    // TODO: Fetch from backend
    setTours(mockTours);
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-purple-100 dark:from-[#0a0a0a] dark:to-[#171717] p-8">
      <h1 className="text-3xl font-bold mb-8 text-foreground">Your Product Tours</h1>
      <div className="grid gap-6 grid-cols-1 sm:grid-cols-2 md:grid-cols-3">
        {tours.map((tour) => (
          <div key={tour.id} className="bg-white dark:bg-[#18181b] rounded-xl shadow-md p-6 border border-gray-100 dark:border-gray-800 flex flex-col gap-2">
            <div className="flex justify-between items-center mb-2">
              <h2 className="font-semibold text-lg text-foreground">{tour.title}</h2>
              <span className={`px-2 py-1 rounded text-xs font-medium ${tour.visibility === "public" ? "bg-blue-100 text-blue-700" : "bg-gray-200 text-gray-700"}`}>
                {tour.visibility}
              </span>
            </div>
            <div className="text-gray-500 text-sm mb-1">{tour.steps} steps</div>
            <div className="text-gray-400 text-xs mb-2">Created: {tour.createdAt}</div>
            <div className="flex items-center gap-2 text-xs text-gray-500">
              <svg width="16" height="16" fill="none" viewBox="0 0 24 24"><path d="M12 5c-7 0-9 7-9 7s2 7 9 7 9-7 9-7-2-7-9-7zm0 10a3 3 0 1 1 0-6 3 3 0 0 1 0 6z" stroke="#6366F1" strokeWidth="1.5"/></svg>
              {tour.views} views
            </div>
            <a href={`/dashboard/tour/${tour.id}`} className="mt-4 inline-block px-4 py-2 rounded bg-blue-600 text-white text-sm font-semibold hover:bg-blue-700 transition">View/Edit</a>
          </div>
        ))}
      </div>
      <div className="mt-10 text-center">
        <a href="/dashboard/tour/new" className="inline-block px-6 py-3 rounded-full bg-purple-600 text-white font-semibold shadow-lg hover:bg-purple-700 transition">+ Create New Tour</a>
      </div>
    </div>
  );
}
